.lanka {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 6px rgba(96, 174, 246, 0.4);
    padding: 10px;
    margin: 0 7px;
}

.ipsum {
    background: url('../image/player.png');
    background-size: cover;
    height: 442px;
    padding: 20px;
    position: relative;
}

.ipsum h1 {
    font-size: 15px;
    color: rgb(255, 255, 255);
    position: absolute;
    bottom: 15%;
}

.ipsum p {
    font-size: 13px;
    color: rgb(255, 255, 255);
    margin: 0 31% 0 0;
    position: absolute;
    bottom: 6%;
}

.trending {
    margin: 20px 0;
}

