@media screen and (max-width: 768px) {
    .player_img {
        width: 100%;
    }

    .ipsum h1 {
        bottom: 20%;
    }

    .ipsum p {
        margin: 0;
    }

    .cricket {
        margin-top: 5%;
    }

    .adipisicing {
        padding: 11px;
    }

    .galle {
        width: 100%;
        margin: 3% 0;
    }

    .previews {
        margin-left: 39px;
    }

    .live-tab {
        display: block;
    }

    .main-footer .ftr-link ul li {
        list-style-type: none;
    }

    .live-tab .evevts {
        width: 100%;
        margin: 0px;
    }

    .match {
        width: 100%;
    }

    .runs h6 {
        margin: 1px 5px;
        font-size: 14px;
        font-weight: normal;
        margin-left: 11px;
        margin-right: 17px;
    }
    .trending_news h6{
        font-size: 10px;
    }
    .srilanka span{
        font-size: 11px;
    }
    .trending_news p{
        font-size: 11px;
    }
    .lanka ul li{
        padding: 0;
        font-size: 11px;
    }
}