input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0
}

input[type=number] {
    -moz-appearance: textfield
}

input {
    border: 0.5px solid transparent;
    background-color: transparent;
    height: 50px;
    color: var(--color-secondary);
    font-size: var(--text-16);
    font-weight: var(--regular);
    padding: 15px
}

input::placeholder {
    color: var(--sub-title)
}

.input-group {
    padding: 20px 0px
}

.input-group label {
    display: block;
    font-size: var(--text-16);
    color: var(--color-secondary);
    margin-bottom: 10px
}

.input-group input {
    border: var(--border);
    width: 100%;
    display: block
}

.input-group:focus-within label {
    color: var(--color-primary)
}

.input-group:focus-within input {
    border-color: var(--color-primary)
}

.input-group label span {
    font-size: 14px;
    color: var(--sub-title)
}

.input-group input::placeholder {
    font-size: 16px;
    color: var(--sub-heading)
}

.checkbox-group {
    position: relative;
    display: inline-block;
    height: 15px;
    width: 15px
}

.checkbox-group .input {
    display: none
}

.checkbox-group .input:checked+span:before {
    border-color: var(--color-primary)
}

.checkbox-group .input:checked+span:after {
    transform: scale(1)
}

.checkbox-group .checked {
    position: absolute;
    top: 50% !important;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    width: 100%;
    cursor: pointer
}

.checkbox-group .checked:before,
.checkbox-group .checked:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: .3s ease-in
}

.checkbox-group .checked:before {
    content: "";
    opacity: 1;
    visibility: visible;
    border: 1px solid var(--sub-title)
}

.checkbox-group .checked:after {
    content: "\eed6";
    font-family: IcoFont !important;
    transform: scale(0);
    font-size: 15px;
    color: var(--color-primary);
    padding-top: 2px;
    display: flex;
    align-items: center;
    justify-content: center
}

.checkbox-group .checked-content {
    padding-left: 25px;
    font-size: var(--text-16);
    color: var(--color-secondary)
}

@media screen and (max-width: 575px) {
    .input-group {
        padding: 15px 0px
    }
}

.review__rating input {
    display: none
}

.review__rating input:checked~label {
    color: #aaa
}

.review__rating.fill input {
    display: none
}

.review__rating.fill input:checked~label {
    color: orange
}

.review__rating label {
    color: orange;
    font-size: 25px
}

.review__rating label.full {
    color: red
}

.star-mark.star-fill .rating>label {
    color: var(--yellow)
}

