.d-block {
    display: block
}

.d-in-block {
    display: inline-block
}

.fixed {
    position: fixed
}

.relative {
    position: relative
}

.absolute {
    position: absolute
}

.unset {
    position: unset
}

.overflow {
    overflow: hidden
}

.overflow-x {
    overflow-x: hidden
}

.overflow-y {
    overflow-y: hidden
}

.x-scroll {
    overflow-x: scroll
}

.y-scroll {
    overflow-y: scroll
}

.rhb-list-shape li {
    position: relative;
    margin-left: 20px !important
}

.rhb-list-shape li:after {
    position: absolute;
    content: "";
    height: 10px;
    width: 10px;
    top: 50%;
    left: -20px;
    transform: translateY(-50%);
    background-color: var(--color-primary);
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    transition: var(--ease-in)
}

.rhb-list-shape li:hover:after {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    height: 2px
}

.title-line-shape {
    position: relative;
    display: inline-block
}

.title-line-shape:after {
    position: absolute;
    content: "";
    height: 3px;
    width: 20px;
    border-radius: 5px;
    top: 50%;
    left: calc(100% + 10px);
    transform: translateY(-50%);
    background-color: var(--color-blue)
}

.title-line-shape:before {
    position: absolute;
    content: "";
    height: 3px;
    width: 8px;
    border-radius: 10px;
    top: 50%;
    left: calc(100% + 40px);
    transform: translateY(-50%);
    background-color: var(--color-blue)
}

.left-line-shape {
    position: relative;
    padding-left: 15px
}

.left-line-shape:after {
    position: absolute;
    content: "";
    top: 50%;
    left: 0;
    height: calc(100% - 10px);
    width: 4px;
    background-color: var(--color-primary);
    transform: translateY(-50%)
}

.round-shape li a {
    position: relative
}

.round-shape li a:after {
    position: absolute;
    content: "";
    top: 50%;
    left: 0;
    height: 5px;
    width: 5px;
    background-color: var(--color-blue);
    border-radius: 50%;
    transform: translateY(-50%);
    transition: var(--ease-in)
}

.round-shape li a:hover:after {
    border-radius: 0px;
    height: 2px;
    width: 8px
}

.input-number-wrapper {
    position: relative;
    width: 100px;
    background-color: var(--bg-primary);
    height: 50px;
    border: var(--border)
}

.input-number-wrapper input {
    max-width: 100%;
    border: none;
    height: 100%;
    background-color: transparent;
    padding: 0px 15px;
    font-size: var(--text-16);
    color: var(--color-secondary);
    font-size: 16px;
    font-weight: var(--medium);
    text-align: center
}

.input-number-wrapper input:focus {
    border: none;
    outline: 0
}

.input-number-wrapper button {
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    color: var(--color-secondary);
    font-size: var(--text-16)
}

.input-number-wrapper .decrease {
    left: 10px
}

.input-number-wrapper .increase {
    right: 10px
}

.label-check {
    position: relative;
    display: inline-block;
    height: 15px;
    width: 15px
}

.label-check .check-input {
    display: none
}

.label-check .check-input:checked+span:after {
    transform: scale(1)
}

.label-check .checked {
    position: absolute;
    top: calc(50% - 1px);
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    width: 100%;
    cursor: pointer;
    border-radius: 50%;
    overflow: hidden
}

.label-check .checked:before,
.label-check .checked:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: .3s ease-in;
    border-radius: 50%
}

.label-check .checked:before {
    content: "";
    opacity: 1;
    visibility: visible;
    border: 1px solid var(--bg-primary)
}

.label-check .checked:after {
    content: "\eed6";
    font-family: IcoFont !important;
    transform: scale(0);
    font-size: 13px;
    color: var(--color-secondary);
    display: flex;
    align-items: center;
    justify-content: center
}

.choice-color .label-check {
    height: 20px;
    width: 20px;
    margin: 0px 10px;
    border-radius: 50%;
    position: relative
}

.choice-color .label-check .check-input {
    display: none
}

.choice-color .label-check .check-input:checked+span:before {
    transform: scale(1);
    visibility: visible;
    opacity: 1
}

.choice-color .label-check .checked {
    height: 100%;
    width: 100%;
    background-color: var(--black);
    overflow: visible;
    position: absolute
}

.choice-color .label-check .checked:after {
    display: none
}

.choice-color .label-check .checked:before {
    border: 2px solid var(--sub-title);
    transform: scale(0.5);
    opacity: 0;
    visibility: hidden;
    height: calc(100% + 8px);
    width: calc(100% + 8px);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important
}

.choice-color.black .label-check .checked:before {
    border: 5px solid var(--sub-title)
}

.choice-color.red .label-check .checked {
    background-color: var(--color-primary)
}

.choice-color.red .label-check .checked:before {
    border: 5px solid var(--sub-title)
}

.choice-color.yellow .label-check .checked {
    background-color: var(--yellow)
}

.choice-color.yellow .label-check .checked:before {
    border: 5px solid var(--sub-title)
}

.ui-slider-wrapper {
    width: 95%;
    padding-left: 15px;
    padding-right: 20px
}

.ui-slider-wrapper .price-filter {
    display: flex;
    align-items: center
}

.ui-slider-wrapper .price-filter #slider-container {
    width: 100%
}

#slider-container {
    height: 5px;
    width: 100%;
    background-color: var(--primary-overlay);
    z-index: 1;
    position: relative;
    margin: 0 auto;
    border-radius: 0px;
    border: none;
    box-shadow: none
}

#slider-container .ui-slider-range {
    height: 100%;
    width: 100%;
    background: transparent;
    z-index: -1
}

#slider-container .noUi-connect {
    background-color: #bcbcbc
}

.noUi-horizontal .noUi-handle {
    width: 20px;
    height: 20px;
    right: -5px;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 50%;
    background: var(--color-secondary);
    border: none;
    cursor: pointer;
    box-shadow: none
}

.noUi-horizontal .noUi-handle:before,
.noUi-horizontal .noUi-handle:after {
    display: none
}

.noUi-horizontal .noUi-handle:focus {
    outline: 0;
    border: none
}

.noUi-horizontal .noUi-handle .noUi-tooltip {
    bottom: unset;
    top: 100%;
    border: none;
    border-radius: 0px;
    background: transparent;
    color: var(--color-secondary);
    padding: 5px;
    margin-top: 15px;
    font-size: 18px
}

.noUi-horizontal .noUi-handle.noUi-handle-upper {
    background: var(--color-primary)
}

