.social-icon {
    display: flex;
    align-items: center
}

.social-icon li {
    height: 32px;
    width: 32px;
    border-radius: 5px;
    box-sizing: border-box;
    margin-right: 20px;
    border: 1px solid var(--color-secondary)
}

.social-icon li:last-child {
    margin-right: 0px
}

.social-icon li a {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%
}

.social-icon li a i {
    font-size: 15px;
    color: var(--color-secondary)
}

.social-icon.social-white li {
    border: 1px solid var(--color-white);
    position: relative;
    overflow: hidden;
    z-index: 1;
    transition: var(--ease-in)
}

.social-icon.social-white li:after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    background-color: var(--color-primary);
    clip-path: circle(0% at 0 0);
    transition: var(--ease-in)
}

.social-icon.social-white li:hover {
    border-color: transparent
}

.social-icon.social-white li:hover:after {
    clip-path: circle(150% at 0 0)
}

.social-icon.social-white li:hover a i {
    color: var(--white)
}

.social-icon.social-white li a i {
    color: var(--color-white);
    transition: var(--ease-in)
}

.social-icon.social-bg-red li a i {
    color: var(--white)
}

@media screen and (max-width: 575px) {
    .social-icon li {
        height: 25px;
        width: 25px
    }

    .social-icon li a i {
        font-size: 12px
    }
}

.contact-info {
    display: flex;
    align-items: center
}

.contact-info li {
    color: var(--color-white);
    display: flex;
    align-items: center;
    margin-right: 30px;
    font-size: var(--text-16)
}

.contact-info li:last-child {
    margin-right: 0px
}

.contact-info li img,
.contact-info li svg {
    max-height: 24px;
    max-width: 34px;
    margin-right: 12px
}

.contact-info li svg path {
    fill: var(--color-primary)
}

@media screen and (max-width: 575px) {
    .contact-info li {
        font-size: 14px
    }

    .contact-info li svg,
    .contact-info li img {
        max-width: 16px;
        margin-right: 10px
    }
}

.search-popup-box {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.1);
    width: 1300px;
    padding: 150px 300px;
    background-color: var(--search-bg);
    visibility: hidden;
    opacity: 0;
    transition: var(--ease-in)
}

.search-popup-box .search-box {
    transform: scale(0.7);
    opacity: 0
}

.search-popup-box.active {
    transform: translate(-50%, -50%) scale(1);
    visibility: visible;
    opacity: 1;
    z-index: 2;
    overflow: hidden
}

.search-popup-box.active .search-box {
    transform: scale(1);
    opacity: 1
}

.search-popup-box.search-popup-bg {
    background-image: url(../img/search-bg.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: transparent !important
}

.search-box {
    display: flex;
    align-items: center;
    position: relative;
    height: 60px;
    background-color: var(--input-bg);
    transition: var(--ease-in)
}

.search-box span {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center
}

.search-box span i {
    font-size: 20px;
    color: var(--color-primary)
}

.search-box input {
    height: 100%;
    width: 100%;
    border: none;
    background-color: transparent;
    padding: 15px 15px 15px 60px;
    color: var(--color-secondary);
    font-size: var(--text-16);
    font-weight: var(--regular);
    border-width: 0.3px 0px 0.3px 0.3px;
    border-style: solid;
    border-color: var(--border-color)
}

.search-box input::placeholder {
    font-size: var(--text-16);
    line-height: 1.5;
    color: var(--sub-title)
}

.search-box button {
    padding: 15px 30px;
    font-size: var(--text-16);
    font-weight: var(--medium);
    color: var(--white);
    background-color: var(--color-primary);
    height: 100%;
    flex: 1;
    display: flex;
    align-items: center
}

.search-box:focus-within input {
    border-color: var(--color-primary)
}

.search-box:focus-within span i {
    color: var(--sub-title)
}

.close-popup {
    position: absolute;
    top: 15px;
    right: 15px;
    height: 30px;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: var(--border);
    cursor: pointer;
    background-color: var(--search-bg);
    border-radius: 3px;
    transition: var(--ease-in)
}

.close-popup i {
    font-size: 20px;
    color: var(--color-secondary);
    transition: var(--ease-in)
}

.close-popup:hover {
    border-color: transparent;
    background-color: var(--color-blue)
}

.close-popup:hover i {
    color: var(--white)
}

@media screen and (max-width: 1400px) {
    .search-popup-box {
        width: 75%;
        padding: 150px 50px
    }

    .search-popup-box .search-box {
        max-width: 550px;
        margin: 0 auto
    }
}

@media screen and (max-width: 991px) {
