}

body:after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
    background-color: var(--body-overlay);
    visibility: hidden;
    opacity: 0;
    transition: .4s ease
}

