.title-heading {
    display: flex;
    justify-content: center;
    position: relative;
    padding: 25px 0px;
    margin-bottom: 50px
}

.title-heading:after {
    position: absolute;
    content: attr(data-title);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: var(--bold);
    font-family: var(--rubik);
    font-size: 120px;
    line-height: 1;
    letter-spacing: -0.07em;
    color: var(--title-color);
    white-space: nowrap;
    z-index: -1
}

.title-heading h2 {
    position: relative;
    font-size: 30px;
    font-weight: var(--medium);
    color: var(--color-secondary);
    position: relative
}

.title-heading h2:after,
.title-heading h2:before {
    position: absolute;
    content: "";
    top: 50%;
    transform: translateY(-50%);
    height: 2px;
    width: 95px;
    background-color: var(--color-primary)
}

.title-heading h2:after {
    left: calc(100% + 10px)
}

.title-heading h2:before {
    right: calc(100% + 10px)
}

@media screen and (max-width: 1350px) {
    .title-heading {
        padding: 50px 0px
    }

    .title-heading:after {
        font-size: 135px
    }

    .title-heading h2:after,
    .title-heading h2:before {
        width: 50px
    }
}

@media screen and (max-width: 991px) {
    .title-heading {
        padding: 35px 0px;
        margin-bottom: 35px
    }

    .title-heading:after {
        font-size: 100px
    }
}

@media screen and (max-width: 767px) {
    .title-heading {
        padding: 0px
    }

    .title-heading:after {
        display: none
    }
}

.preloader-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: var(--body-bg);
    z-index: 999999;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column
}

.preloader-wrapper.hide {
    display: none;
    visibility: hidden
}

.preloader-wrapper #place-heading {
    max-width: 50%;
    margin: 0 auto;
    position: absolute;
    top: 100px;
    left: auto
}

body {
    overflow: hidden
}

body.remover {
    overflow: visible;
    overflow-x: hidden
}

@keyframes moveY {
    0% {
        transform: translateY(-10px)
    }

    100% {
        transform: translateY(10px)
