.diamond {
    position: relative;
    width: 60px;
    height: 60px;
    transform-style: preserve-3d;
    transform-origin: center center;
    animation: spin 1s linear infinite
}

.diamond span:first-of-type {
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    border: 4px solid var(--color-primary);
    transform: rotateX(90deg) rotateZ(45deg);
    opacity: 0.7
}

.diamond span:nth-of-type(2) {
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    border: 4px solid var(--color-primary);
    transform: rotateX(90deg) rotateY(90deg) rotateZ(45deg);
    opacity: 0.7
}

.diamond span:last-of-type {
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    border: 4px solid var(--color-primary);
    transform: rotateZ(45deg);
    opacity: 0.7
}

.movement {
    position: relative;
    width: 60px;
    height: 60px
}

@keyframes falloff {
    0% {
        top: 30px;
        height: 0px;
        opacity: 0
    }

    50% {
        top: 0;
        height: 30px;
        opacity: 1
    }

    100% {
        top: 0;
        height: 0px;
        opacity: 0
    }
}

.movement span:first-of-type {
    position: absolute;
    left: 0;
    top: 0;
    width: 4px;
    height: 30px;
    border-radius: 4px;
    background-color: var(--light-gray100);
    opacity: 0;
    animation: falloff 1s ease infinite
}

@keyframes falloff-2 {
    0% {
        height: 0px;
        opacity: 0
    }

    50% {
        height: 20px;
        opacity: 1
    }

    100% {
        height: 0px;
        opacity: 0
    }
}

.movement span:first-of-type::after {
    content: '';
    position: absolute;
    top: 40px;
    left: 0;
    width: 4px;
    height: 20px;
    border-radius: 4px;
    background-color: var(--light-gray100);
    opacity: 0;
    animation: falloff-2 1s ease infinite
}

@keyframes falloff-3 {
    0% {
        top: 40px;
        height: 0px;
        opacity: 0
    }

    50% {
        top: 20px;
        height: 20px;
        opacity: 1
    }

    100% {
        top: 20px;
        height: 0px;
        opacity: 0
    }
}

.movement span:nth-of-type(2) {
    position: absolute;
    top: 20px;
    left: calc(50% - 2px);
    width: 4px;
    height: 20px;
    border-radius: 4px;
    background-color: var(--light-gray100);
    opacity: 0;
    animation: falloff-3 1s ease infinite
}

@keyframes falloff-4 {
    0% {
        height: 0px;
        opacity: 0
    }

    50% {
        height: 40px;
        opacity: 1
    }

    100% {
        height: 0px;
        opacity: 0
    }
}

.movement span:nth-of-type(2)::after {
    content: '';
    position: absolute;
    top: 30px;
    left: 0;
    width: 4px;
    height: 40px;
    border-radius: 4px;
    background-color: var(--light-gray100);
    opacity: 0;
    animation: falloff-4 1s ease infinite
}

@keyframes falloff-5 {
    0% {
        top: 30px;
        height: 0px;
        opacity: 0
    }

    50% {
        top: 0;
        height: 30px;
        opacity: 1
    }

    100% {
        top: 0;
        height: 0px;
        opacity: 0
    }
}

.movement span:last-of-type {
    position: absolute;
    top: 0;
    right: 0;
    width: 4px;
    height: 30px;
    border-radius: 4px;
    background-color: var(--light-gray100);
    opacity: 0;
    animation: falloff-5 1s ease infinite
}

